.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #f0a8f6 0%, #f66fd022 60%);
}

.Offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.Offers-left h1{
    color: #171717;
    font-size: 80px;
    font-weight: 600;

}

.Offers-left p{
    color: #171717;
    font-size: 22px;
    font-weight: 40;
    font-weight: 600;

}

.Offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.Offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}

